<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div class="task pageSpace h100">
        <div>
            <a-form :label-width="80">
                <a-row type="flex" justify="space-around" align="middle">
                    <a-col :span="5">
                        <a-input addon-before="IP" placeholder="请输入IP"/>
                    </a-col>
                    <a-col :span="5">
                        <a-input placeholder="请输入IP"/>
                        <template v-slot:addonBefore>
                            <a-select style="width: 90px" v-model="arr">
                                <a-select-option value="scc">scc</a-select-option>
                            </a-select>
                        </template>
                    </a-col>
                    <a-col :span="5">
                        <a-input addon-before="地区" placeholder="请输入地区"/>
                    </a-col>
                    <a-col :span="5">
                    <a-input addon-before="邮箱名" placeholder="请输入邮箱名"/>
                </a-col>
                </a-row>
            <br>
                <a-row type="flex" justify="space-around" align="middle">
                    <a-col :span="5">
                        <a-input addon-before="操作" placeholder="请输入操作"/>
                    </a-col>
                    <a-col :span="5">
                        <a-input addon-before="协议" placeholder="请输入协议"/>
                    </a-col>
                    <a-col :span="5">
                        <a-range-picker name="date" v-model="value1" @change="onChange" show-time>
                        </a-range-picker>
                    </a-col>
                    <a-col :lg="2" style="float: right">
                        <a-button type="primary">查询</a-button>
                    </a-col>
                    <a-col :lg="2" style="float: right">
                        <a-button type="primary">导出</a-button>
                    </a-col>
                </a-row>
            </a-form>
        </div>
        <div style="margin-top: 40px">
            <a-table :columns="columns" rowKey="dataIndex" :pagination="false">
            </a-table>
        </div>
    </div>
</template>

<script>
    export default {
        name: "webLoginAnalyze",
        data(){
            return{
                arr:'as',
                value1:null,
                columns: [
                    {
                        dataIndex: "username",
                        title: "邮箱名",
                    },
                    {
                        dataIndex: "req_ip",
                        title: "请求IP",
                    },
                    {
                        dataIndex: "country_name",
                        title: "国家",
                    },
                    {
                        dataIndex: "pt_type",
                        title: "协议类型",
                    },
                    {
                        dataIndex: "result",
                        title: "登录状态",
                    },
                    {
                        dataIndex: "fulltime",
                        title: "完整时间"
                    }
                ],
            }
        },
        methods:{
            onChange(date,dateString){
                console.log(date,dateString)
            }
        }
    }
</script>

